import Vue from 'vue';
import Router from 'vue-router';
import Home from './components/Home';
import Info from './components/Info';

Vue.use(Router);

export default new Router({
   routes: [
      {
         path: '/',
         name: 'home-page',
         component: Home
      }, {
         path: '/docs',
         name: 'docs-page',
         component: Info
      },
      {
         path: '*',
         redirect: '/'
      }
   ]
})



